import { Trigger } from './types';

export function triggerToString(t: Trigger) {
  switch (t.type) {
    case 'clock':
      return `${t.hz} Hz`;
    case 'key down':
      return `${t.key} down`;
    case 'key up':
      return `${t.key} up`;
    case 'pointer down':
    case 'pointer up':
    case 'pick up':
    case 'drop':
      return t.type;
  }
}

export function triggerFromString(s: String): Trigger | null {
  const parts = s.toLowerCase().split(' ');

  if (parts.length === 1) {
    switch (parts[0]) {
      case 'drop':
        return { type: 'drop' };
    }
  } else if (parts.length === 2) {
    if (parts[0] === 'pick' && parts[1] === 'up') {
      return { type: 'pick up' };
    } else if (parts[0] === 'pointer') {
      if (parts[1] === 'down') {
        return { type: 'pointer down' };
      } else if (parts[1] === 'up') {
        return { type: 'pointer up' };
      }
    }
    switch (parts[1]) {
      case 'hz': {
        const value = parseInt(parts[0]);
        return !Number.isNaN(value) ? { type: 'clock', hz: value } : null;
      }
      case 'down':
        return { type: 'key down', key: parts[0] };
      case 'up': {
        return { type: 'key up', key: parts[0] };
      }
    }
  }
  return null;
}

(Array.prototype as any).forEachBackwards = function (fn: (x: any, idx: number) => void) {
  for (let idx = this.length - 1; idx >= 0; idx--) {
    fn(this[idx], idx);
  }
};
