import type { Rect } from './types';

const ANGLE_INCREMENT = 5;

let nextId = 1;

export class Costume {
  private static sampleCostumesByName = new Map<string, Costume>();
  private static readonly byName = new Map<string, Costume>();

  static get(name: string) {
    const costume = this.byName.get(name);
    if (costume) {
      return costume;
    } else {
      throw new Error('invalid costume name: ' + name);
    }
  }

  static getRandom(): Costume {
    const idx = Math.floor(Math.random() * this.byName.size);
    return [...this.byName.values()][idx];
  }

  static add(element: HTMLCanvasElement | HTMLImageElement) {
    const id = `costume_${nextId++}`;
    this.byName.set(id, this.fromElement(element));
    return id;
  }

  static clearAndLoadSampleCostumes() {
    this.byName.clear();

    if (this.sampleCostumesByName.size === 0) {
      for (const img of document.getElementsByTagName('img')) {
        console.log(img.id, img.width, img.height);
        this.byName.set(img.id, this.fromElement(img));
      }

      this.drawCostume(
        100,
        100,
        (ctx) => {
          ctx.fillStyle = 'red';
          ctx.beginPath();
          ctx.fillRect(0, 0, 100, 100);
          ctx.fill();
        },
        'red square',
      );

      this.drawCostume(
        100,
        100,
        (ctx) => {
          ctx.fillStyle = 'yellow';
          ctx.beginPath();
          ctx.fillRect(0, 0, 100, 100);
          ctx.fill();
        },
        'yellow square',
      );

      for (let [name, costume] of this.byName) {
        this.sampleCostumesByName.set(name, costume);
      }
    } else {
      for (let [name, costume] of this.sampleCostumesByName) {
        this.byName.set(name, costume);
      }
    }
  }

  static drawCostume(
    w: number,
    h: number,
    fn: (ctx: CanvasRenderingContext2D) => void,
    name?: string,
  ) {
    const canvas = document.createElement('canvas');
    canvas.width = w;
    canvas.height = h;

    const ctx = canvas.getContext('2d')!;
    fn(ctx);

    if (name) {
      this.byName.set(name, this.fromElement(canvas));
      return name;
    } else {
      return this.add(canvas);
    }
  }

  static computePopulatedArea(ctx: CanvasRenderingContext2D, w: number, h: number) {
    const imageData = ctx.getImageData(0, 0, w, h).data;
    let minX = w;
    let minY = h;
    let maxX = 0;
    let maxY = 0;
    for (let y = 0; y < h; y++) {
      for (let x = 0; x < w; x++) {
        const idx = (y * w + x) * 4;
        const alpha = imageData[idx + 3];
        if (alpha > 0) {
          minX = Math.min(minX, x);
          minY = Math.min(minY, y);
          maxX = Math.max(maxX, x);
          maxY = Math.max(maxY, y);
        }
      }
    }
    return {
      minX,
      maxX,
      minY,
      maxY,
      ox: w / 2 - minX,
      oy: h / 2 - minY,
    };
  }

  static makeShrinkWrappedCanvas(element: HTMLImageElement | HTMLCanvasElement) {
    const canvas = document.createElement('canvas');
    canvas.width = element.width;
    canvas.height = element.height;
    const ctx = canvas.getContext('2d', { willReadFrequently: true })!;
    ctx.drawImage(element, 0, 0);
    const { minX, maxX, minY, maxY } = this.computePopulatedArea(ctx, canvas.width, canvas.height);
    canvas.width = maxX - minX;
    canvas.height = maxY - minY;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(element, -minX, -minY);
    return canvas;
  }

  static fromElement(element: HTMLImageElement | HTMLCanvasElement): Costume {
    const canvas = this.makeShrinkWrappedCanvas(element);

    // make a canvas in which we can draw every rotation of the costume
    const bigCanvas = document.createElement('canvas');
    const scale = 0.25; // use a smaller image to pre-compute bounding boxes (big speed-up!)
    const bigCanvasSize = Math.ceil(
      Math.sqrt((scale * canvas.width) ** 2 + (scale * canvas.height) ** 2),
    );
    bigCanvas.width = bigCanvasSize;
    bigCanvas.height = bigCanvasSize;
    const ctx = bigCanvas.getContext('2d', { willReadFrequently: true })!;

    const metricsByAngle: Rect[] = [];
    for (let angle = 0; angle < 360; angle += ANGLE_INCREMENT) {
      ctx.clearRect(0, 0, bigCanvasSize, bigCanvasSize);
      ctx.save();
      ctx.translate(bigCanvas.width / 2, bigCanvas.height / 2);
      const angleInRadians = (angle * Math.PI) / 180;
      ctx.rotate(angleInRadians);
      ctx.scale(scale, scale);
      ctx.drawImage(canvas, -(canvas.width / 2), -canvas.height / 2);
      ctx.restore();
      const {
        minX,
        maxX,
        minY,
        maxY,
        ox: x,
        oy: y,
      } = this.computePopulatedArea(ctx, bigCanvasSize, bigCanvasSize);
      metricsByAngle.push({
        x: x / scale,
        y: y / scale,
        w: (maxX - minX) / scale,
        h: (maxY - minY) / scale,
      });
    }

    return new Costume(element, canvas, metricsByAngle);
  }

  readonly ctx: CanvasRenderingContext2D;

  private constructor(
    readonly element: HTMLImageElement | HTMLCanvasElement, // TODO: consider removing this
    readonly canvas: HTMLCanvasElement,
    readonly metricsByIncrement: Rect[],
  ) {
    this.ctx = canvas.getContext('2d', { willReadFrequently: true })!;
    // for (let angle = 0; angle < 360; angle += ANGLE_INCREMENT) {
    //   console.log('at', angle, metricsByIncrement[angle / ANGLE_INCREMENT]);
    // }
  }

  getMetricsAtAngle(angle: number): Rect {
    // first, normalize the heading to the nearest multiple of ANGLE_INCREMENT in [0, 360)
    const normalizedAngle =
      (((Math.round(angle / ANGLE_INCREMENT) * ANGLE_INCREMENT) % 360) + 360) % 360;
    const idx = normalizedAngle / ANGLE_INCREMENT;
    // now grab the size from the pre-computed table
    return this.metricsByIncrement[idx];
  }
}
